.main-nav {
  border-top: 1px solid $border_grey;
  border-bottom: 1px solid $border_grey;

  @media (max-width: $desktop-width) {
    border: none;
  }
}

.main-nav__wrapper {
  display: flex;
  flex-direction: row;

  @media (max-width: $desktop-width) {
    flex-direction: column;

    .page__closed & {
      position: absolute;
      top: -500px;
    }
  }
}
